<template>
    <View>
        <View v-for="i in data" style="padding: 1rem;height: 20rem;">
            <View>
                <Image :src="i.img" style="width:6em;float: left;margin-right: 1em;"></Image>
                <View>{{i.title}}</View>
                <View style="font-size:1.2rem">{{i.desc}}</View>
                <View style="font-size:1.2rem;width: 60%;height: 2em;white-space: nowrap;text-overflow: ellipsis;overflow: hidden;">
                    主演：{{i.star}}
                </View>
                <View style="background-color: #5aa700;width: 6em;text-align: center;float: left;color: #fff;border-radius: 5em;padding: 0.1em 1em;">
                    立即播放
                </View>
            </View>
            <View style="float: left;width: 100%;margin-top: 1em;">
                <Text class="btn" style="display: inline-block; width: 18%; text-align: center; height: 3.5em; line-height: 3.5em; background-color: rgb(238, 238, 238); margin-left: 0px;">1</Text>
                <Text class="btn" style="display: inline-block; width: 18%; text-align: center; height: 3.5em; line-height: 3.5em; background-color: rgb(238, 238, 238); margin-left: 0px;">2</Text>
                <Text class="btn" style="display: inline-block; width: 18%; text-align: center; height: 3.5em; line-height: 3.5em; background-color: rgb(238, 238, 238); margin-left: 0px;">...</Text>
                <Text class="btn" style="display: inline-block; width: 18%; text-align: center; height: 3.5em; line-height: 3.5em; background-color: rgb(238, 238, 238); margin-left: 0px;">14</Text>
                <Text class="btn" style="display: inline-block; width: 18%; text-align: center; height: 3.5em; line-height: 3.5em; background-color: rgb(238, 238, 238); margin-left: 0px;">15</Text>
            </View>
        </View>
    </View>
</template>
<script>
    export default {
        props: ['data'],
        methods: {
            "swiper_end": function(index) {
                console.log('swiper touchend ' + index, this)
                this.$emit('swiper_touchend', index);
            },
            "scroll_end":function(){
                this.$emit('scroll_end');
            }
        },
    }
</script>



