<template>
    <View @scroll_end="scroll_end">
        <View style="position:fixed;width:100%;text-align: center;vertical-align:center;height: 5rem;line-height:5rem;border-bottom:1px solid #eee;z-index:10;background: #fff">
            <QiyiInput
                    style="display:inline-block;width: 90%;height:3.2rem;background-color: #eee;border: none;border-radius: 2.2rem;outline:none;padding:0 1rem"
                    @click="click_search" :value="input_value"></QiyiInput>
        </View>
        <ScrollView style="padding-top:5rem;">
            <result v-if="data.length>0" :data="data"></result>
        </ScrollView>
        <search style="z-index:11;" :search_show="searchShow" @search="search"/>
    </View>
</template>
<script>
    import search from './search/index.vue';
    import resultList from './resultList.vue';

    const url = "https://search.video.iqiyi.com/o?channel_name=&if=html5&pageNum=1&pageSize=20&limit=20&category=&timeLength=0&releaseDate=&key=%E7%BB%9D%E5%91%BD%E6%AF%92%E5%B8%88&start=1&sortKey=&threeCategory=&u=29cb6b073def9e1592c10a42e1f31983&qyid=29cb6b073def9e1592c10a42e1f31983&pu=&video_allow_3rd=1&intent_result_number=10&intent_category_type=1&vfrm=2-3-0-1&t=1501829475708&from=weixin_lite_app";

    export default {
        name: 'index',
        data: function() {
            return {
                input_value: '绝命毒师',
                searchShow: false,
                data: [],
            };
        },
        components: {
            'search': search,
            'result': resultList
        },
        methods: {
            "click_search": function() {
                this.searchShow = true;
            },
            "swiper_touchend": function(index) {
                this.bannerIndex = index;
            },
            "search": function(value) {
                qiyiApi.request(url, {}, result => {
                    let temp = result.data.docinfos;
                    let list = [];
                    for(let i in temp) {
                        list.push({
                            img: temp[i].albumDocInfo.albumVImage,
                            title: temp[i].albumDocInfo.albumTitle,
                            desc: [temp[i].albumDocInfo.releaseDate, temp[i].albumDocInfo.channel, temp[i].albumDocInfo.region, temp[i].albumDocInfo.video_lib_meta.language].join(' '),
                            star: temp[i].albumDocInfo.star,
                            category: temp[i].albumDocInfo.video_lib_meta.category
                        })
                    }
                    this.data = list;
                });
                this.searchShow = false;
            },
            "scroll_end": function() {
                qiyiApi.request(url, {}, result => {
                    let temp = result.data.docinfos;
                    let list = [];
                    for(let i in temp) {
                        list.push({
                            img: temp[i].albumDocInfo.albumVImage,
                            title: temp[i].albumDocInfo.albumTitle,
                            desc: [temp[i].albumDocInfo.releaseDate, temp[i].albumDocInfo.channel, temp[i].albumDocInfo.region, temp[i].albumDocInfo.video_lib_meta.language].join(' '),
                            star: temp[i].albumDocInfo.star,
                            category: temp[i].albumDocInfo.video_lib_meta.category
                        })
                    }
                    this.data = this.data.concat(list);
                });
            }
        },
        mounted: function() {
            console.log('index mounted');
        },
        updated: function() {
            console.log('updated');
        }
    }
</script>



